<template>

<div class="table-view schades" id="pagingtop">
	<div class="card">
		<div class="card-header">
			<h3><span>Mijn schades</span></h3>
		</div>

		<!-- LOADER -->
        	<loader-component v-if="!pageDataLoaded"></loader-component>
        <!-- /LOADER -->

		<!-- hide table till data is available -->
		<table class="table" v-if="pageDataLoaded">
		  <thead>
		    <tr>
		      <th>Datum</th>
		      <th>Schadenr.</th>
		      <th>Verzekering</th>
		      <th>Soort schade</th>
		      <th>Status</th>
		    </tr>
		  </thead>
		  <tbody v-for="schade in chunkedArray[currentPage]" :key="schade.schadenummer">
		    <tr class="main" :class="{open: schade.isOpen}" @click="schade.isOpen = !schade.isOpen">
		      <td>{{ schade.schadedatum }}</td>		      
		      <td>{{ schade.schadenummer }}</td>
		      <td>
					<a :href="ctx + '/' + schade.polisUrl">{{ schade.produktNaam }} </a>
					<span v-if="schade.kenteken">| {{ schade.kenteken }}</span>
					<span v-if="schade.produktTypeNaam == 'woonhuis' || schade.produktTypeNaam == 'inboedel'">| {{ schade.straat }}</span>
		      <td>{{ schade.schadesoort }}</td>
		      <td>
					<span class="tag" :class="[{'tag-danger':schade.schadestatus == 'wacht op uw reactie'}, {'tag-success':schade.schadestatus == 'in behandeling'}, {'tag-success':schade.schadestatus == 'beoordeling'}, {'tag-success':schade.schadestatus == 'ontvangen'}]">
						{{ schade.schadestatus }}
					</span>
			  </td>
		    </tr>
		    <tr class="details" :class="{open: schade.isOpen}">
					<td colspan="3">
						<div>
							<span class="icon clean" v-on:click='schade.isOpen = !schade.isOpen'>0</span>
							<p class="tag" :class="[{'tag-danger':schade.schadestatus == 'wacht op uw reactie'}, {'tag-success':schade.schadestatus == 'in behandeling'}, {'tag-success':schade.schadestatus == 'beoordeling'}, {'tag-success':schade.schadestatus == 'ontvangen'}]">{{ schade.schadestatus }}</p>
							<div v-show="schade.isOpen">
								<label>Soort schade</label>
								<p>{{ schade.schadesoort }}</p>
								<label v-if="schade.kenteken">Kenteken</label>
								<p>{{ schade.kenteken }}</p>
								<label v-if="schade.produktTypeNaam == 'woonhuis' || schade.produktTypeNaam == 'inboedel'">Adres</label>
								<p>{{ schade.straat }}</p>
							</div>
						</div>
					</td>
				</tr>
		  </tbody>
		</table>
		
		<div id="error"></div>
		
		<nav class="paging" v-if="totalPages > 1">
		  <ul class="pagination">
		    <li class="page-item">
		    	<button class="previous btn btn-secondary" @click="currentPage -= 1" v-if="currentPage > 0" data-href="#pagingtop" type="button">Vorige</button>
		    </li>
		    <li class="page-item" v-for="pageNumber in totalPages" :key="pageNumber">
		    	<button class="btn btn-secondary" @click="currentPage = pageNumber - 1" :class="{active: pageNumber === currentPage + 1}" data-href="#pagingtop" type="button">{{ pageNumber }}</button>
		    </li>
		    <li class="page-item">
		    	<button class="next btn btn-secondary" @click="currentPage += 1" v-if="currentPage < totalPages - 1" data-href="#pagingtop" type="button">Volgende</button>
		    </li>
		  </ul>
		</nav>
		<p class="aantalfacturen" v-if="pageDataLoaded">
			<span v-if="totaalSchades === 1"><strong>{{ totaalFacturen }}</strong> schade gevonden</span>
			<span v-else><strong>{{ totaalSchades }}</strong> schades gevonden</span>
		</p>
	</div>
</div>
</template>

<script>

export default {
  name: 'mijnschades-component',
  props: {
	  ctx: String
  },
  data: function () {
    return {    	
		isOpen: false,
    	currentPage: 0,
        itemsPerPage: "",
        totalPages: "",
        chunkedArray: [],
    	pageToShow: "",
    	totaalSchades: "",
		pageDataLoaded: false
    }
	},
  mounted: function () {		
	var self = this;
	$.ajax({
		url: this.ctx + "/services/mijnschades/getall",
		type: 'GET',
		success: function(mijnschades) {
			mijnschades.forEach(function (d) {
      		   return self.$set(d, 'isOpen', false);
      		});

			var currentPage = 0;
			self.pageDataLoaded = true;
			
			let i, j, chunkedArray = [], chunk = 5;
		    for (i=0, j=0; i < mijnschades.length; i += chunk, j++) {
		        chunkedArray[j] = mijnschades.slice(i,i+chunk);
		    }
		    self.totalPages = chunkedArray.length;
		    self.itemsPerPage = chunk;
		    self.pageToShow = chunkedArray[currentPage];
		    self.chunkedArray = chunkedArray;
		    self.totaalSchades = mijnschades.length;
		      
			return chunkedArray;			 
		},
		error: function() {
			$('#error').html('<p>U heeft nog geen schades gemeld</p>');
		}
	});	
  }
}

</script>




