<template>
<div class="card schadeprocess">
	<div class="card-header">
		<h3>
			<span>			
				Hoe verloopt het schadeproces?
			</span>
		</h3>				
	</div>
	
		<!-- LOADER -->
        	<loader-component v-if="!pageDataLoaded"></loader-component>
        <!-- /LOADER -->

			<!-- The v-if is necessary as the data needed won't be available till the ajax call is successful and complete, 
			so we don't want to render the component with only undefined data (which results in an error anyway) -->
	<div class="card-block" v-if="pageDataLoaded">
		<!-- menu -->

		<div class="process-menu messages">
			<div :class="[{'mobile-hide': polissen.length > 3}, {'hide': polissen.length === 1}, {'desktop-hide': polissen.length > 8}]">
				<ul class="nav nav-tabs flex-container">
					<li v-for="(polis, index) in polissen" 
						:key="polis.polisnummer"
						@click="currentTab = index"
						class="nav-item">
						<a v-bind:class="['nav-link', { active: currentTab === index }]">
							{{ polis.produktTypeNaam }}<br />
							<span v-if="polis.kenteken">{{ polis.kenteken }}</span>
							<span v-if="polis.produktTypeNaam == 'woonhuis' || polis.produktTypeNaam == 'inboedel'">{{ polis.straat }}</span>
						</a>
					</li>					
				</ul>
			</div>
		</div>

		<div :class="[{'desktop-hide': polissen.length < 9}, {'mobile-hide': polissen.length < 4}, {'hide': polissen.length === 1}]">
			<span>Selecteer een verzekering:</span>
			<select class="form-control" id="tab_selector" v-model="selected" @change="selectedTab">
				<option disabled value="">Selecteer een verzekering</option>
				<option v-for="(polis, index) in polissen"
					:key="index"
					:value="index">
						{{ polis.produktTypeNaam }}
					<span v-if="polis.kenteken">- {{ polis.kenteken }}</span>
					<span v-if="polis.produktTypeNaam == 'woonhuis' || polis.produktTypeNaam == 'inboedel'">- {{ polis.straat }}</span>
				</option>
			</select>
			<br />
		</div>

		<!-- content -->		
			<keep-alive>
				<component
					:is = "currentTabComponent"	
					:produktnaam = "currentTabName"	
					:schadeurl = "currentSchadeUrl"
				></component>
			</keep-alive>
	</div>  
</div> 
</template>

<script> 

import SchadeprocesAuto from './auto.vue'
import SchadeprocesCaravan from './caravan.vue'
import SchadeprocesInboedel from './inboedel.vue'
import SchadeprocesReis from './reis.vue'
import SchadeprocesOngevallen from './ongevallen.vue'
import SchadeprocesAansprakelijkheid from './aansprakelijkheid.vue'
import SchadeprocesRechtsbijstand from './rechtsbijstand.vue'
import SchadeprocesWoonhuis from './woonhuis.vue'


export default {
  name: 'schadeproces-component',
  components: {
	  'schadeproces-auto': SchadeprocesAuto,
	  'schadeproces-reis': SchadeprocesReis, 
	  'schadeproces-caravan': SchadeprocesCaravan,
	  'schadeproces-inboedel': SchadeprocesInboedel,
	  'schadeproces-ongevallen': SchadeprocesOngevallen,
	  'schadeproces-aansprakelijkheid': SchadeprocesAansprakelijkheid,
	  'schadeproces-rechtsbijstand': SchadeprocesRechtsbijstand,
	  'schadeproces-woonhuis' : SchadeprocesWoonhuis
  },
  props: {	  
	ctx: String
  },
  data: function () {
    return {    	
		currentTab: 0,
		polissen: [],
		produktnaam:'',
		schadeurl:'',
		pageDataLoaded: false,
		selected: {} // this one is needed to make the selectbox work its magic
     }
  },
	methods: {
	  selectedTab () {
		var self = this;
		self.currentTab = this.selected;
		return self.currentTab
	}
  },
  mounted: function () {		
	var self = this;
	$.ajax({
		url: this.ctx + "/services/schadeproces/getall",
		type: 'GET',
		success: function(polissen) {
			self.polissen = polissen;	
			self.pageDataLoaded = true;		
		},
		error: function() {
			$('#error').html('<p>Er zijn geen verzekeringen gevonden</p>');
		}
	});	
  },
  computed: {
    currentTabComponent: function () {
	  return 'schadeproces-' + this.polissen[this.currentTab].produktTypeNaam
	},
	currentTabName: function () {
		return this.polissen[this.currentTab].produktNaam
	},
	currentSchadeUrl: function () {
		return this.polissen[this.currentTab].schademeldenurl
	}
  }

}

</script>




