// include vue components when an element with the id="vue" exists
var vm = new Vue({
  el: '#vue',
  methods: {
	  resizeIframe: function(frame){
			var curHeight =  $(frame).contents().find('body').height() + 75;
			$(frame).css('height', curHeight + 'px' );
	  },
	  setIframeUrl : function(iframeSelector, serviceMethod) {
			$.getJSON("services/urlservice/" + serviceMethod, function( data ) {
				if(data != null) {
					$(iframeSelector).prop('src', data.url);
				}
			});
	  }
  }
});


global.vm = vm;



